import { Link } from 'gatsby';
import React, { FC } from 'react';
import { ArticlePreviewInfo } from '../../types';
import { InternalLinks } from '../../utils/links';
import { Divider, Text } from '../common';
import ArticleCardItem, { CardSize } from '../common/ArticleCardItem/ArticleCardItem';
import CustomLink from '../common/Link';
import {
  ArticleMetaInformation,
  NewsItems,
  ReturnLink,
  StyledArticleHeader,
  StyledLatestNewsBlock,
} from './styled';
import { formatIsoStringToDisplayDate } from '../../utils/dates';

interface ArticleHeaderProps {
  articleType: string;
  date: string;
  lastUpdatedOn?: string;
}

interface LatestNewsBlockProps {
  latestNews: ArticlePreviewInfo[];
}

export const ArticleHeader: FC<ArticleHeaderProps> = ({
  articleType,
  date,
  lastUpdatedOn,
}) => {
  return (
    <StyledArticleHeader>
      <ReturnLink>
        <Link to={InternalLinks.LatestNews}>Return to Latest News</Link>
      </ReturnLink>
      <Divider />
      <ArticleMetaInformation>
        <p>{articleType}</p>
        {lastUpdatedOn && (
          <p>Updated:{' ' + formatIsoStringToDisplayDate(lastUpdatedOn)}</p>
        )}
        <p>Published:{' ' + date}</p>
      </ArticleMetaInformation>
    </StyledArticleHeader>
  );
};

export const LatestNewsBlock: FC<LatestNewsBlockProps> = ({ latestNews }) => {
  return (
    <StyledLatestNewsBlock>
      <Divider />
      <Text.Title.Bold.md>Latest news</Text.Title.Bold.md>
      <NewsItems>
        {latestNews.slice(0, 3).map((articleItem) => (
          <ArticleCardItem
            article={articleItem}
            key={articleItem.articleId}
            cardSize={CardSize.S}
          />
        ))}
      </NewsItems>
      <div>
        <CustomLink address={InternalLinks.LatestNews} arrow>
          Browse all articles
        </CustomLink>
      </div>
      <Divider />
    </StyledLatestNewsBlock>
  );
};
