export const formatIsoStringToDisplayDate = (dateString: string): string => {
  //MMMM DD, YYYY
  const months = new Array(
    'January',
    'February',
    'March',
    'April',
    'May',
    'June',
    'July',
    'August',
    'September',
    'October',
    'November',
    'December',
  );
  const date = new Date(dateString)
  const dateByParts = date.toDateString().split(' ');
  const currentMonth = months[date.getMonth()]


  return `${currentMonth} ${dateByParts[2]}, ${dateByParts[3]}`;
};

export const ONE_DAY_IN_MS = 86400000
export const TIME_30_SECONDS = 30000
